import React from 'react';

import FormInput from './FormInput';
import styles from './Form.module.css';

const Form = ({ fields, data, handler }) => {
  return (
    <div className={styles.form}>
      {fields.map(field => (
        <FormInput key={field.name} {...{ field, data, handler }} />
      ))}
    </div>
  );
};

export default Form;
