import React from 'react';

import { get } from 'utils/object';
import { getValidationError } from 'utils/validators';

import Input from 'components/Input';

const FormInput = ({
  field: { name, label, validators, type },
  data,
  handler
}) => {
  const value = get(name, data, '');
  const error = getValidationError(value, validators, data);

  switch (type) {
    default:
      return (
        <Input
          key={name}
          name={name}
          type={type}
          label={label}
          valid={!error}
          error={error}
          value={value}
          onChange={handler(name)}
        />
      );
  }
};

export default FormInput;
