import { useReducer, useCallback, useMemo } from 'react';
import { get, set, clone } from 'utils/object';
import { getValidationError } from 'utils/validators';

const formReducer = (state, { action, payload: { name, value } }) => {
  switch (action) {
    case 'set':
      return set(name, value, clone(state));
    default:
      return state;
  }
};

const useFormData = (fields, initialData) => {
  const [data, dispatch] = useReducer(formReducer, initialData);

  const handler = useCallback(
    name => value => {
      dispatch({ action: 'set', payload: { name, value } });
    },
    [dispatch]
  );

  const valid = useMemo(
    () =>
      fields.every(
        ({ validators, name }) =>
          getValidationError(get(name, data, ''), validators, data) === null
      ),
    [data, fields]
  );

  return [data, valid, handler];
};

export default useFormData;
