import React from 'react';
import Page from 'components/Page';
import WithFooter from 'components/WithFooter';
import Button from 'components/Button';
import Form from 'components/Form';
import Footer from 'components/Footer';

import { password, isConfirmFor, minLength } from 'utils/validators';

import useFormData from 'components/Form/useFormData';

const ChangePasswordForm = ({ history }) => {
  const fields = [
    {
      name: 'currentPassword',
      type: 'password',
      label: 'Current Password',
      validators: [password]
    },
    {
      name: 'password',
      type: 'password',
      label: 'New Password',
      validators: [password]
    },
    {
      name: 'confirmPassword',
      type: 'password',
      label: 'Confirm New Password',
      validators: [
        minLength(1, ' '),
        isConfirmFor('password', 'Passwords do not match!')
      ]
    }
  ];

  const [data, valid, handler] = useFormData(fields, {});

  const submit = () => {
    if (valid) {
      console.log(data);
      history.goBack();
    } else {
      // eslint-disable-next-line no-alert
      window.alert('Password do not match');
    }
  };

  return (
    <Page
      className="lowLevelPage"
      title="Reset password"
      onBackNavigation={() => history.goBack()}
    >
      <WithFooter
        footer={
          <Footer>
            <Button disabled={!valid} onClick={submit}>
              Update
            </Button>
          </Footer>
        }
      >
        <Form {...{ fields, data, handler }} />
      </WithFooter>
    </Page>
  );
};

export default ChangePasswordForm;
