function cloneArray(arr) {
  return arr.map(value => {
    if (Array.isArray(value)) {
      return cloneArray(value);
    }
    if (typeof value === 'object' && value !== null) {
      /* eslint-disable-next-line */
      return cloneObject(value);
    }
    if (value instanceof Date) {
      return new Date(value);
    }
    return value;
  });
}

function cloneObject(obj) {
  const keys = Object.keys(obj);

  return keys.reduce((acc, k) => {
    const value = obj[k];

    if (Array.isArray(value)) {
      acc[k] = cloneArray(value);
    } else if (typeof value === 'object' && value !== null) {
      acc[k] = cloneObject(value);
    } else if (value instanceof Date) {
      acc[k] = new Date(value);
    } else {
      acc[k] = value;
    }

    return acc;
  }, {});
}

export const clone = obj => {
  return cloneObject(obj);
};

export const set = (key, value, obj) => {
  let path = key;
  if (typeof path === 'string') {
    path = path.split('.');
  }
  if (path.length === 1) {
    // eslint-disable-next-line
    obj[path[0]] = value;
    return obj;
  }
  if (obj[path[0]] === null || obj[path[0]] === undefined) {
    // eslint-disable-next-line
    obj[path[0]] = {};
  }
  set(path.slice(1), value, obj[path[0]]);
  return obj;
};

export const get = (key, obj, defaultValue) => {
  if (obj === null || obj === undefined) {
    return defaultValue !== undefined ? defaultValue : null;
  }
  let path = key;
  if (typeof path === 'string') {
    path = path.split('.');
  }
  if (path.length === 1) return obj[path[0]] || defaultValue;
  return get(path.slice(1), obj[path[0]]);
};
