import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect
} from 'react-router-dom';
import PageTransition from 'react-router-page-transition';
import AccountSettings from 'containers/AccountSettings';
import EditFieldForm from 'containers/EditFieldForm';
import EditCategoriesForm from 'containers/EditCategoriesForm';
import ChangePasswordForm from 'containers/ChangePasswordForm';
import IframePage from './IframePage';
import FaqPage from './FaqPage';

import * as validators from '../utils/validators';

const App = () => {
  return (
    <Router baseUrl="/inspoapp-web-view-server/">
      <Route
        render={({ location }) => (
          <PageTransition timeout={500}>
            <Switch location={location}>
              <Route
                exact
                path="/"
                component={() => (
                  <div>
                    <Redirect to="/creator" />
                  </div>
                )}
              />
              <Route
                exact
                path={['/creator', '/creator.html']}
                component={props => (
                  <AccountSettings userRole="creator" {...props} />
                )}
              />
              <Route
                exact
                path={['/consumer', '/consumer.html']}
                component={props => (
                  <AccountSettings userRole="consumer" {...props} />
                )}
              />
              <Route
                path="/edit/username"
                component={props => (
                  <EditFieldForm
                    name="username"
                    pageTitle="Edit Username"
                    label="Username"
                    initialValue="spotravnyy"
                    validator={validators.username}
                    {...props}
                  />
                )}
              />
              <Route
                path="/edit/email"
                component={props => (
                  <EditFieldForm
                    name="email"
                    type="email"
                    pageTitle="Edit email"
                    label="Email"
                    initialValue="spotravnyy@lohika.com"
                    validator={validators.email}
                    {...props}
                  />
                )}
              />
              <Route
                path="/edit/categories"
                component={props => <EditCategoriesForm {...props} />}
              />
              <Route
                path="/edit/password"
                component={props => <ChangePasswordForm {...props} />}
              />

              <Route
                path="/consumer/faq"
                component={props => <FaqPage {...props} />}
              />
              <Route
                path="/creator/faq"
                component={props => <FaqPage creator {...props} />}
              />

              <Route
                path="/privacy_policy"
                component={props => (
                  <IframePage
                    pageTitle="Privacy Policy"
                    url="https://www.insponetwork.com/pages/privacy_policy/index.html?mobile"
                    {...props}
                  />
                )}
              />

              <Route
                path="/terms_of_use"
                component={props => (
                  <IframePage
                    pageTitle="Terms of Use"
                    url="https://www.insponetwork.com/pages/terms_and_conditions/index.html?mobile"
                    {...props}
                  />
                )}
              />

              {/* In case of not matched route - redirect to consumer page */}
              <Route
                component={() => (
                  <div>
                    <Redirect to="/consumer" />
                  </div>
                )}
              />
            </Switch>
          </PageTransition>
        )}
      />
    </Router>
  );
};

export default App;
