import React from 'react';
import native from 'utils/mediator';
import Page from 'components/Page';
import List, { ListItem } from 'components/List';
import AppVersion from 'components/AppVersion';
import WithFooter from 'components/WithFooter';
import Footer from 'components/Footer';
import SignOutButton from 'containers/SignOutButton';

const AccountSettings = ({ history, userRole = 'consumer' }) => {
  const email =
    localStorage.getItem('form-value-email') || 'spotravnyy@lohika.com';
  const username = localStorage.getItem('form-value-username') || 'spotravnyy';

  return (
    <Page
      className="topLevelPage"
      noGap={userRole === 'creator'}
      title="Settings"
      onBackNavigation={() => native.navigateBack()}
    >
      <WithFooter
        footer={
          <Footer noBottomGap>
            <SignOutButton />
            <AppVersion />
          </Footer>
        }
      >
        <List>
          {userRole === 'creator' && (
            <ListItem onClick={() => native.navigateProfileSetup()}>
              Edit Profile
            </ListItem>
          )}
          <ListItem
            title="Username"
            onClick={() => history.push('/edit/username')}
          >
            {username}
          </ListItem>

          <ListItem title="Email" onClick={() => history.push('/edit/email')}>
            {email}
          </ListItem>

          <ListItem onClick={() => history.push('/edit/password')}>
            Update Password
          </ListItem>

          {userRole === 'creator' && (
            <ListItem onClick={() => history.push('/edit/categories')}>
              Edit categories
            </ListItem>
          )}

          {userRole === 'creator' && (
            <ListItem onClick={() => native.navigateLinkedAccounts()}>
              Linked Accounts
            </ListItem>
          )}

          <ListItem onClick={() => history.push(`/${userRole}/faq`)}>
            FAQs
          </ListItem>

          <ListItem onClick={() => native.openEmail()}>Email Feedback</ListItem>

          <ListItem onClick={() => history.push('/privacy_policy')}>
            Privacy Policy
          </ListItem>

          <ListItem onClick={() => history.push('/terms_of_use')}>
            Terms of Use
          </ListItem>
        </List>
      </WithFooter>
    </Page>
  );
};

export default AccountSettings;
