import { identity } from './fn';
import { get } from './object';

export const username = v => {
  if (v.length < 3) return ' ';
  if (!/^[a-z0-9_]{7,22}$/g.test(v))
    return 'Brand Name should be 7-22 characters.';
  if (/(inspo|admin)/.test(v)) return 'Should not contain `inspo` or `admin`.';
  return null;
};

export const password = v => {
  if (v.length < 3) return ' ';
  if (v.length < 6)
    return 'Password should have at least 6 letters and numbers.';
  if (!/[0-9]/g.test(v)) return 'Password should contain numbers.';

  return null;
};

export const email = v => {
  if (v.length < 3) return ' ';
  if (
    /* eslint-disable-next-line */
    !/^(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9]))\.){3}(?:(2(5[0-5]|[0-4][0-9])|1[0-9][0-9]|[1-9]?[0-9])|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/g.test(
      v
    )
  )
    return 'Incorrect format';

  return null;
};

const createRequiredValidator = (
  fn,
  error = 'Validation Failed!',
  requiredMessage = 'This field is required!'
) => (val, data) => {
  if (val === undefined) return requiredMessage;
  return fn(val, data) ? null : error;
};

const createOptionalValidator = (fn, error = 'Validation Failed!') => (
  val,
  data
) => {
  if (val === undefined) return null;
  return fn(val, data) ? null : error;
};

export const not = (fn, error = 'Validation failed!') => (value, data) => {
  if (fn(value, data) === null) return error;
  return null;
};

export const isPresent = (error = 'This field is required!') =>
  createRequiredValidator(x => x !== undefined, error);

export const matches = (
  pattern,
  error = 'Field does not match required pattern!'
) => createOptionalValidator(x => pattern.test(x), error);

export const minLength = (len, error = 'Field value is too short!') =>
  createOptionalValidator(x => x.length >= len, error);

export const maxLength = (len, error = 'Field value is too long!') =>
  createOptionalValidator(x => x.length <= len, error);

export const isConfirmFor = (name, error = 'Values did not match!') =>
  createRequiredValidator((v, data) => get(name, data) === v, error);

export const getValidationError = (value, validators, data) =>
  validators.map(v => v(value, data)).filter(identity)[0] || null;
